.switch-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
}
    
.switch-wrapper > div {
    position: absolute;
    width: 100%;
}

.custom-ratio-content {
    position: absolute !important;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.press-btn:hover {
    transform: scale(1.1);
    /* transform-origin: center center; */
}

.press-btn:active {
    /* transform: scale(0.9); */
}

.mdc-text-field__input:focus {
    outline: 1px solid white !important;
}

.page-root-div {
    background: black;
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.unselectable {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}