.drone-player {
    position: absolute;
    bottom: 0px;
    left: 0px;
    height: 16%;
    width: 19%;
    animation-timing-function: cubic-bezier(0.12, 0.7, 0.44, 1.01); 
}

.drone-player.is-dropping {
    animation-name: dropping;
}

.block-item {
    position: absolute;
    height: 15%;
    width: 14%;
}

.block-item > div {
    width: 100%;
    height: 100%;
    background-image: url(../assets/images/drone-game/eagle.gif);
    background-repeat: no-repeat;
    background-size: contain;
}

.block-item.is-touched > div {
    animation: collision-down 2s;
    animation-timing-function: ease-out;
    animation-iteration-count: 1;
}

.mountain-item-1 {
    position: absolute;
    height: 26%;
    width: 27%;
    background-image: url(../assets/images/drone-game/mountain-1.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.mountain-item-2 {
    position: absolute;
    height: 35%;
    width: 34%;
    background-image: url(../assets/images/drone-game/mountain-2.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.mountain-item-3 {
    position: absolute;
    height: 26%;
    width: 27%;
    background-image: url(../assets/images/drone-game/mountain-3.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.wind-item {
    position: absolute;
    height: 100%;
    width: 18.4%;
    background-image: url(../assets/images/drone-game/wind.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.wind-item.is-touched {
    position: absolute;
    height: 100%;
    width: 18.4%;
    background-image: url(../assets/images/drone-game/wind.png);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.ground-item {
    position: absolute;
    height: 15%;
    width: 8%;
    z-index: 1;
}

.ground-item > div {
    width: 100%;
    height: 100%;
    background-image: url(../assets/images/drone-game/wait-water.png);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    animation: jump 0.8s infinite;
    animation-timing-function: ease-out;
}
    
.ground-item.is-touched > div {
    background-image: url(../assets/images/drone-game/plant.png);
    animation: shown 0.5s;
    animation-iteration-count: 1;
}

@keyframes collision-down {
    0% { transform: translate(0px, 0px);}
    100% { transform: translate(30vw, 70vh); }
}

@keyframes jump {
    0% { transform: translate(0px, 0px); }
    100% { transform: translate(0px, -3vh); }
}

@keyframes shown {
    0% { opacity: 0.5; }
    100% { opacity: 1.0; }
}


