.drone-player {
    position: absolute;
    bottom: 0px;
    left: 0px;
    height: 16%;
    width: 19%;
    animation-timing-function: cubic-bezier(0.12, 0.7, 0.44, 1.01); 
}

.bullet-div > div {
    width: 2.5%;
    height: 1.6%;
    left: 19%;
    position: absolute;
    background-image: url(../assets/images/attack-game/shooting-bullet.png);
    background-repeat: no-repeat;
    background-size: contain;
    animation: shoot-bullet 3s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
}

.bullet-hit {
    display: none;
}

.block-item > div {
    width: 100%;
    height: 100%;
    background-image: url(../assets/images/attack-game/eagle.gif);
    background-repeat: no-repeat;
    background-size: contain;
}

.block-item.hit > div {
    width: 100%;
    height: 100%;
    background-image: url(../assets/images/attack-game/eagle-attack.gif);
    background-repeat: no-repeat;
    background-size: contain;
}

@keyframes shoot-bullet {
    0% { transform: translate(0%, 0%);}
    100% { transform: translate(3500%, 0%); }
}